// src/contexts/DemoContext.jsx
// Easy Scurit - Contexte de gestion de la dmo 3 minutes

import React, { createContext, useContext, useState, useEffect, useCallback } from 'react';
import { supabase } from '../config/supabase';

const DemoContext = createContext();

export const useDemo = () => {
  const context = useContext(DemoContext);
  if (!context) {
    // Retourner des valeurs par dfaut si pas de provider
    return {
      isDemoMode: false,
      demoExpired: false,
      timeRemaining: 180,
      demoRequest: null,
      startDemo: () => {},
      restoreDemo: () => false,
      endDemo: () => {},
      isFeatureLocked: () => false,
      formatTimeRemaining: () => '3:00',
      DEMO_DURATION: 180
    };
  }
  return context;
};

// Dure de la dmo en secondes (3 minutes)
const DEMO_DURATION = 180;

export const DemoProvider = ({ children }) => {
  const [isDemoMode, setIsDemoMode] = useState(false);
  const [demoStartTime, setDemoStartTime] = useState(null);
  const [timeRemaining, setTimeRemaining] = useState(DEMO_DURATION);
  const [demoExpired, setDemoExpired] = useState(false);
  const [demoSessionId, setDemoSessionId] = useState(null);
  const [demoRequest, setDemoRequest] = useState(null);

  // Timer de la dmo - TOUTES LES SECONDES
  useEffect(() => {
    let interval;
    
    if (isDemoMode && demoStartTime && !demoExpired) {
      interval = setInterval(() => {
        const elapsed = Math.floor((Date.now() - demoStartTime) / 1000);
        const remaining = Math.max(0, DEMO_DURATION - elapsed);
        
        setTimeRemaining(remaining);
        
        if (remaining <= 0) {
          setDemoExpired(true);
          setIsDemoMode(false);
          clearInterval(interval);
          localStorage.removeItem('demo_session');
        }
      }, 1000);
    }
    
    return () => {
      if (interval) clearInterval(interval);
    };
  }, [isDemoMode, demoStartTime, demoExpired]);

  // Restaurer la dmo au chargement
  useEffect(() => {
    restoreDemo();
  }, []);

  /**
   * Dmarrer une session de dmo
   */
  const startDemo = useCallback(async (requestData) => {
    try {
      const now = Date.now();
      
      // Enregistrer dans Supabase
      const { data, error } = await supabase
        .from('demo_sessions')
        .insert({
          email: requestData.email,
          telephone: requestData.telephone,
          prenom: requestData.prenom,
          nom: requestData.nom,
          entreprise: requestData.entreprise,
          siret: requestData.siret,
          ville: requestData.ville,
          domaines_demandes: requestData.domaines || requestData.modulesInteresses,
          profil_demande: requestData.profil || requestData.typeActivite,
          nb_utilisateurs: requestData.nb_utilisateurs || requestData.nombreTechniciens,
          tarif_calcule: requestData.tarif_final || requestData.tarif,
          options_selectionnees: requestData.options || [],
          rapports_fournis: requestData.rapports_fournis || {},
          started_at: new Date().toISOString(),
          expires_at: new Date(now + DEMO_DURATION * 1000).toISOString()
        })
        .select()
        .single();

      if (error) {
        console.error('Erreur cration session dmo:', error);
      }

      // Dmarrer la dmo
      setDemoSessionId(data?.id || 'local-demo');
      setDemoRequest(requestData);
      setDemoStartTime(now);
      setTimeRemaining(DEMO_DURATION);
      setDemoExpired(false);
      setIsDemoMode(true);
      
      // Stocker en localStorage
      localStorage.setItem('demo_session', JSON.stringify({
        id: data?.id || 'local-demo',
        startTime: now,
        request: requestData
      }));
      
      return true;
    } catch (error) {
      console.error('Erreur startDemo:', error);
      return false;
    }
  }, []);

  /**
   * Restaurer une session de dmo
   */
  const restoreDemo = useCallback(() => {
    try {
      const stored = localStorage.getItem('demo_session');
      if (stored) {
        const { id, startTime, request } = JSON.parse(stored);
        const elapsed = Math.floor((Date.now() - startTime) / 1000);
        
        if (elapsed < DEMO_DURATION) {
          setDemoSessionId(id);
          setDemoRequest(request);
          setDemoStartTime(startTime);
          setTimeRemaining(DEMO_DURATION - elapsed);
          setDemoExpired(false);
          setIsDemoMode(true);
          return true;
        } else {
          localStorage.removeItem('demo_session');
          setDemoExpired(true);
        }
      }
    } catch (error) {
      console.error('Erreur restoreDemo:', error);
    }
    return false;
  }, []);

  /**
   * Terminer la dmo
   */
  const endDemo = useCallback(async (converted = false) => {
    try {
      if (demoSessionId && demoSessionId !== 'local-demo') {
        await supabase
          .from('demo_sessions')
          .update({
            converted,
            converted_at: converted ? new Date().toISOString() : null
          })
          .eq('id', demoSessionId);
      }
    } catch (error) {
      console.error('Erreur endDemo:', error);
    }
    
    localStorage.removeItem('demo_session');
    setIsDemoMode(false);
    setDemoStartTime(null);
    setTimeRemaining(DEMO_DURATION);
    setDemoExpired(false);
    setDemoSessionId(null);
    setDemoRequest(null);
  }, [demoSessionId]);

  /**
   * Vrifier si une fonctionnalit est verrouille
   */
  const isFeatureLocked = useCallback((feature) => {
    if (!isDemoMode) return false;
    
    const lockedFeatures = [
      'create_report', 'edit_report', 'generate_pdf', 'export_data',
      'add_client', 'add_site', 'settings', 'billing'
    ];
    
    return lockedFeatures.includes(feature);
  }, [isDemoMode]);

  /**
   * Formater le temps restant (MM:SS)
   */
  const formatTimeRemaining = useCallback(() => {
    const minutes = Math.floor(timeRemaining / 60);
    const seconds = timeRemaining % 60;
    return `${minutes}:${seconds.toString().padStart(2, '0')}`;
  }, [timeRemaining]);

  const value = {
    isDemoMode,
    demoExpired,
    timeRemaining,
    demoRequest,
    startDemo,
    restoreDemo,
    endDemo,
    isFeatureLocked,
    formatTimeRemaining,
    DEMO_DURATION
  };

  return (
    <DemoContext.Provider value={value}>
      {children}
    </DemoContext.Provider>
  );
};

export default DemoContext;
